article, aside, details, figcaption, figure, footer, header, hgroup, nav, section, summary {
  display: block; }

audio, canvas, video {
  display: inline-block; }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden] {
  display: none; }

html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%; }

a:focus {
  outline: thin dotted; }

a:active, a:hover {
  outline: 0; }

h1 {
  font-size: 2em; }

abbr[title] {
  border-bottom: 1px dotted; }

b, strong {
  font-weight: 700; }

dfn {
  font-style: italic; }

mark {
  background: #ff0;
  color: #000; }

code, kbd, pre, samp {
  font-family: monospace, serif;
  font-size: 1em; }

pre {
  white-space: pre-wrap;
  word-wrap: break-word; }

q {
  quotes: \201C \201D \2018 \2019; }

small {
  font-size: 80%; }

sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -.5em; }

sub {
  bottom: -.25em; }

img {
  border: 0; }

svg:not(:root) {
  overflow: hidden; }

fieldset {
  border: 1px solid silver;
  margin: 0 2px;
  padding: .35em .625em .75em; }

button, input, select, textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0; }

button, input {
  line-height: normal; }

button, html input[type=button],
input[type=reset], input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer; }

button[disabled], input[disabled] {
  cursor: default; }

input[type=checkbox], input[type=radio] {
  box-sizing: border-box;
  padding: 0; }

input[type=search] {
  -webkit-appearance: textfield;
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  box-sizing: content-box; }

input[type=search]::-webkit-search-cancel-button, input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none; }

textarea {
  overflow: auto;
  vertical-align: top; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

body, figure {
  margin: 0; }

legend, button::-moz-focus-inner, input::-moz-focus-inner {
  border: 0;
  padding: 0; }

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0; }

* {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 18px; }

.field-container {
  max-width: 670px;
  margin: 50px auto; }
  .field-container .field {
    display: grid;
    grid-template-columns: 200px 200px 200px 30px;
    grid-template-rows: 40px;
    grid-gap: 10px;
    margin-bottom: 20px; }
    .field-container .field input {
      padding: 5px 15px;
      border: 1px solid grey;
      border-radius: 4px;
      transition: all 200ms; }
      .field-container .field input:focus {
        outline: none;
        border-color: #005fbf;
        box-shadow: 0px 0px 2px 2px rgba(53, 139, 230, 0.5); }
    .field-container .field .close {
      font-size: 1.5em;
      color: grey;
      display: grid;
      align-items: center;
      justify-items: center;
      cursor: pointer;
      transition: all 200ms; }
      .field-container .field .close:hover {
        transform: scale(1.2);
        color: red; }
  .field-container .btn {
    height: 36px;
    margin-top: 20px;
    margin-right: 10px;
    margin-bottom: 50px;
    border-radius: 3px;
    display: grid;
    align-items: center;
    justify-items: center;
    cursor: pointer;
    transition: all 200ms; }
  .field-container .btn-add {
    width: 150px;
    grid-template-columns: 30px 120px;
    grid-template-rows: 1fr;
    justify-items: end;
    color: #1184f7;
    margin-bottom: 20px; }
    .field-container .btn-add:hover {
      color: #005fbf; }
  .field-container .btn-apply {
    width: 80px;
    color: white;
    background-color: #1184f7;
    border: 0px;
    float: left; }
    .field-container .btn-apply:hover {
      background-color: #005fbf; }
  .field-container .btn-clear {
    width: 130px;
    background-color: #f4f2f2;
    color: #565656;
    border: 1px solid grey; }
    .field-container .btn-clear:hover {
      background-color: #eaeaea; }
